<template></template>

<script>
import * as dd from "dingtalk-jsapi";
import auth from "@/utils/auth";
import axios from "axios";

export default {
  name: "dingLogin2",

  created() {
    this.$vuetify.theme.dark = true;
  },

  beforeDestroy() {
    this.$vuetify.theme.dark = false;
  },

  methods: {
    getUrlKey(name) {
      return (
        decodeURIComponent(
          (new RegExp("[?|&]" + name + "=" + "([^&;]+?)(&|#|;|$)").exec(
            location.href
          ) || [, ""])[1].replace(/\+/g, "%20")
        ) || null
      );
    },

    doLogin() {
      alert(window.location);
      const corpIdParam = this.getUrlKey("corpid");
      if (!corpIdParam) {
        alert("请求不合法");
        return;
      }

      alert(corpIdParam);
      
      // 登录
      const _this = this;
      dd.ready(function() {
        dd.runtime.permission.requestAuthCode({
          corpId: corpIdParam,
          onSuccess: function(info) {
            // 进行登陆
            const userCodeParam = info.code;
            alert(userCodeParam);
          },
          onFail: function(err) {
            alert("失败");
          }
        });
      });
    }
  },

  mounted() {
    this.doLogin();
  }
};
</script>
